.header{
    display: flex;
    padding: 1rem;
    /* padding-left: 30px; */
    background-color: #1B1811;
    align-items: center;
    justify-content: space-between;
}
.header__logoContainer > img{
    width: 100px;
    height: 100px;
    object-fit: contain;
    border-radius: 50%;
}

.header__siteName > h1{
    color: goldenrod;
    padding-left: 5rem;
}
.header__SearchContainer> input{
    padding: 7px 20px;
    outline: none;
    border-radius: 20px;
}

@media screen and (max-width:1000px) {
    .header{
        
        text-align: center;
    }
    .header__SearchContainer{
        display: none;
    }
    .header__siteName > h1{
        padding: 0;
        flex: 1;
        text-align: center;
       
    }
}
@media screen and (max-width:500px) {
    .header__siteName >h1{
        font-size:1rem;
    }
    .header__logoContainer > img{
        width: 70px;
        height: 70px;
    }
}


/* Sub Header */
.subHeader{
    display: flex;
    align-items: center;
    padding-bottom: .2rem;
    justify-content: space-between;
}

.subheader__SearchContainer > input{
    padding: 7px 20px;
    outline: none;
    border-radius: 20px;
    border: 1px solid #1B1811 ;
}
.subHeader__iconContainer{
    text-align: center;
    width: 3rem;
    height: 3rem;
    /* background-color: #1B1811; */
    color: goldenrod !important;
   
}

@media screen and (min-width:1000px) {
    .subHeader{
        display: none;
    }
}