.calculatorInput{
    margin: 1rem 2rem;
    box-shadow: -1px 4px 20px -6px rgba(0, 0,0, .7);
    text-align: center;
    border-radius: 20px;
    padding: 3rem;
    /* height: 60vh; */
    background-color: #1B1811;
}
.calculatorInput__form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    width: 50%;
    margin: 0 auto;
}
.calculatorInput__form > input,label{
    display: block;
}
.calculatorInput__form > input{
    padding: 10px 2rem;
}
.calculatorForm__allInputs > label{
    /* padding: 10px 10px;
    border-radius: 20px; */
    margin-top: 1rem;
    margin-bottom: 10px;
    font-size: 1.56rem;
}
.StartingDate> input{
    padding: 2px 1rem;
    border-radius: 5px;
    font-size: 1rem;
    border:.1px thin lightgray;
}
.EndDateSelaector > input{
    padding: 2px 1rem;
    border-radius: 5px;
    font-size: 1rem;
    width: max-content;
}
.IntrestRate > input{
    padding: 2px 1rem;
    border-radius: 5px;
    font-size: 1rem;
}
.PrincipeAmount > input{
    padding: 2px 1rem;
    border-radius: 5px;
    font-size: 1rem;
    margin-bottom: 5px;
    
}
.calculatorForm__allInputs > label{
    margin-top: 5px;
    color: goldenrod;
    font-size: 1.15rem;
    font-weight: 700;
    text-align: center;
}
.EndDateSelaector{
   display: flex;
   flex-direction:column;
   align-items: center;
}
.calculatorForm__allInputs > input{
    text-align: center;
    background-color: white;
    
}
