.calculatorInput__calculatorBtn{
    margin-top: 20px;
    padding: 10px 40px;
    background-color: goldenrod;
    color: white;
    font-size: 1rem;
    border-radius: 20px;
    outline: none;
    border: none;
}
.calculatorInput__calculatorBtn:hover{
    opacity: .5;
}
.calculatorForm__allInputs > input:focus{
    border-color: goldenrod;
}
