.calculatorOutputContainer{
    margin: 1rem 2rem;
    box-shadow: -1px 4px 20px -6px rgba(0, 0,0, .7);
    text-align: center;
    border-radius: 20px;
    padding: 1rem;
    background-color: #1B1811;
}
.calculatorOutput{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.mainCalculationOutput{
    display: flex;
    flex-direction: column;
    width: 100%;

}
.mainCalculatorOutput_upperOutputs{
    padding: .1rem;
}
.mainCalculatorOutput_upperOutputs{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.mainCalculatorOutput_upperOutputs_block{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   margin-bottom: 1rem;
   margin-top: 1rem;
}
.mainCalculatorOutput_upperOutputs_block > h3{
    margin-bottom: 1rem;
    color: goldenrod;
}
.mainCalculatorOutput__intrest__principle{
    margin-left: 5px;
}
.mainCalculatorOutput__total{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.block{
    width: 7.5rem;
    height: 2.5rem;
    box-shadow: -1px 4px 20px -6px rgba(0, 0,0, .7);
    text-align: center;
    border-radius: 5px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}
.mainCalculatorOutput__total > h3{
    color: goldenrod;
    margin-bottom: 5px;
}
.mainCalculatorOutput__month__day{
    margin-bottom: -10px;
}
@media screen and (max-width:500px) {
    .mainCalculatorOutput_upperOutputs{
        
        justify-content: space-around;
    }
}
